import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Device } from '@ionic-native/device/ngx';
import { AlertController } from '@ionic/angular';
import { Actas } from 'src/app/interfaces/actas';

import { DataActa } from 'src/app/interfaces/data-acta';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { TipoPermiso } from 'src/app/interfaces/tipo-permiso';
import { DataBaseService } from 'src/app/services/SST/data-base.service';
import { DataService } from 'src/app/services/SST/data.service';
import { ContextService } from 'src/app/services/context.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-consultar-permiso',
  templateUrl: './consultar-permiso.page.html',
  styleUrls: ['./consultar-permiso.page.scss'],
})
export class ConsultarPermisoPage implements OnInit {
  terminados: Actas[] = [];
  tipoPermiso: string;
  listaTipoPermiso: TipoPermiso[] = [];
  fechaInicio: Date;
  fechaFin: Date;
  dataActa: DataActa;
  user: any;
  empresa: any;
  identificacion: string;
  permiso: string;

  currentPage: number = 1;
  workersPerPage: number = 10;
  totalPage: number = 0;

  constructor(
    private data: DataBaseService,
    private utils: UtilsService,
    private alertController: AlertController,
    private context: ContextService,
    private dataService: DataService,
    private device: Device,
    private router: Router
  ) {}

  ngOnInit() {
    this.context.get().then(c => {
      this.user = c.trabajador;
    });
    this.tiposDePermiso();
  }

  getUuid(): string {
    return this.device.uuid;
  }

  async preDescargar(link: string) {
    let alert = this.alertController.create({
      message: '¿Desea descargar el archivo?',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
        },
        {
          text: 'Descargar',
          handler: () => {
            window.open(link, '_system');
          },
        },
      ],
    });
    (await alert).present();
  }

  onChangePermiso(event: any) {
    this.tipoPermiso = event.detail.value;
  }

  tiposDePermiso(): void {
    this.data.open().then(() => {
      this.data.getAllTipoPermiso().subscribe({
        next: (listaTipoPermiso: TipoPermiso[]) => {
          this.listaTipoPermiso = listaTipoPermiso;
          console.log('Lista de tipo de permiso:', this.listaTipoPermiso);
        },
        error: error => {
          console.error('Error al obtener los tipos de permiso:', error);
          this.data.close();
        },
        complete: () => {
          console.log('Tipos de permiso completados');
        },
      });
    });
  }

  async continuar() {
    try {
      await this.utils.presentLoading();
      let user = await this.context.get();
      const fechaInicio = this.fechaInicio ? this.formatearFecha(this.fechaInicio.toString()) : '';
      const fechaFin = this.fechaFin ? this.formatearFecha(this.fechaFin.toString()) : '';
      const tipoPermiso = this.tipoPermiso ? this.tipoPermiso : '';
      const numUserEmpleado = this.identificacion ? this.identificacion : '';
      const permiso = this.permiso ? this.permiso : '';

      //  datos no obligatorios para dataActa fechaInicio, fechaFin, tipoPermiso
      this.dataActa = {
        intTipoPermiso: tipoPermiso,
        strFechaInicio: fechaInicio,
        strFechaFin: fechaFin,
        strIdentificacionTrabajador: numUserEmpleado,
        strNumIdentificacionUsuario: user['_trabajador'].id.toString(),
        strTipoDocEmpresa: user['_empresa'].tipoDocumento,
        strNumIdentificacionEmpresa: user['_empresa'].id.toString(),
        strIdActa: permiso,
      };

      let { status, data } = await this.dataService.listarActas(this.dataActa);

      if (status === 200) {
        if (data.length === 0) {
          this.alertController
            .create({
              header: 'Información',
              message: 'No se encontraron resultados',
              buttons: ['Aceptar'],
            })
            .then(alert => {
              alert.present();
            });
        }
        console.log('data');

        const shorted = data.sort((a, b) => (Number(b['strActa']) - Number(a['strActa'])) as number);
        this.terminados = shorted;
        this.totalPage = Math.ceil(this.terminados.length / this.workersPerPage);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.utils.dismissLoading();
    }
  }

  private formatearFecha(fecha: string): string {
    const date = new Date(fecha);

    const dia = date.getDate();
    const mes = date.getMonth() + 1;
    const anio = date.getFullYear();

    const fechaFormateada = `${dia.toString().padStart(2, '0')}/${mes.toString().padStart(2, '0')}/${anio.toString()}`;

    return fechaFormateada;
  }

  previousPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
    }
  }

  nextPage() {
    if (this.currentPage * this.workersPerPage < this.terminados.length) {
      this.currentPage++;
    }
  }
  back() {
    this.router.navigate(['/home/permisos']);
  }
}
